.form-label {
  position: relative;
}
.control__input[type="checkbox"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  position: absolute;
}
.control__input[type="checkbox"] + .check__box {
  position: absolute;
  width: 30px;
  height: 30px;
  background-image: url("../img/check-off.svg");
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-width: 768px) {
  .control__input[type="checkbox"] + .check__box {
    width: 40px;
    height: 40px;
  }
}
.control__input[type="checkbox"]:checked + .check__box {
  background-image: url("../img/check-hover-on.svg");
}
.control__input[type="checkbox"]:hover + .check__box {
  background-image: url("../img/check-hover-off.svg");
}
.control__input[type="checkbox"]:hover:checked + .check__box {
  background-image: url("../img/check-hover-on.svg");
}
.control__input[type="checkbox"]:disabled + .check__box {
  background-image: url("../img/check-disable-off.svg");
}
.control__input[type="checkbox"]:disabled:checked + .check__box {
  background-image: url("../img/check-disable-on.svg");
}
.control__input[type="checkbox"]:focus + .check__box {
  background-color: #f2f2f2;
}
.check__box + .control__lead {
  padding-left: 45px;
}
@media (min-width: 768px) {
  .check__box + .control__lead {
    padding-left: 60px;
  }
}
.control__input[type="radio"] {
  position: absolute;
  -webkit-appearance: none;
  appearance: none;
}
.control__input[type="radio"] + .radiobutton {
  position: absolute;
  width: 30px;
  height: 30px;
  background-image: url("../img/rb-off.svg");
  background-size: contain;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .control__input[type="radio"] + .radiobutton {
    width: 40px;
    height: 40px;
  }
}
.control__input[type="radio"]:checked + .radiobutton {
  background-image: url("../img/rb-on.svg");
}
.control__input[type="radio"]:checked:hover + .radiobutton {
  background-image: url("../img/rb-hover-on.svg");
}
.control__input[type="radio"]:hover + .radiobutton {
  background-image: url("../img/rb-hover-off.svg");
}
.control__input[type="radio"]:disabled + .radiobutton {
  background-image: url("../img/rb-disable-off.svg");
}
.control__input[type="radio"]:checked:disabled + .radiobutton {
  background-image: url("../img/rb-disable-on.svg");
}
.control__input[type="radio"]:focus + .radiobutton {
  background-color: #f2f2f2;
  border-radius: 50%;
}
.radiobutton + .control__lead {
  display: inline-block;
  padding-left: 45px;
  margin-bottom: 18px;
}
@media (min-width: 768px) {
  .radiobutton + .control__lead {
    padding-left: 60px;
  }
}
